body,html {
  margin: 0;
}
.content-wrapper {
  padding: 40px 160px;
}
.header {
  background: url(./static/header_bg.svg);
  background-repeat: no-repeat;
  background-position: right;
}

.header .content-wrapper {
  display: flex;
  align-items: center;
}

.header .header-text p {
  color: #6A6A6A;
  line-height: 20px;
  letter-spacing: 1px;
}

h1 {
  font-size: 44px;
  color: #1C1E52;
  margin-top: 0;
}

h1 b{
  color: #DD2C00;
}

.header .header-text b.subtext{
  display: block;
  color: #1C1E52;
  margin-top: 32px;
}

.header .header-text a.button{
  color: #fff;
  background: #DD2C00;
  padding: 8px 16px;
  border-radius: 4px;
  margin-top: 16px;
  display: inline-block;
}

.features .feature {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #6A6A6A;
}

.features .feature > *{
  margin: 24px;
}

.feature .feature-desc {
  border: 2px solid #1C1E52;
  padding: 24px;
  box-shadow: 16px 16px 0px #DD2C00;
}

.feature .feature-desc b {
  color: #1C1E52;
}

.feature:nth-child(2n) {
  flex-direction: row-reverse;
}

.features h1 {
  font-size: 32px;
  width: 50%;
  margin: auto;
  text-align: center;
}

.game .content-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}

.game .instruction {
  padding: 24px;
}
.game .instruction {
  color: #6A6A6A;
  cursor: pointer;
}

.game .instruction b {
  font-size: 24px;
  color: #1C1E52;
}

.game .instruction:hover {
  box-shadow: 0px 0px 16px rgba(0,0,0,0.2);
  border-radius: 8px;
}

.instructions i {
  display: block;
  margin-left: 24px;
  color: #DD2C00;
  margin-bottom: 24px;
}
.game img {
  max-height: 80vh;
}
.banner {
  background: #1C1E52;
  color: #fff;
  max-width: 80%;
  margin: auto;
  border-radius: 16px;
  padding: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.banner > * {
  margin: 24px;
}

.banner h2 {
  margin: 0;
}

@media(max-width: 1280px) {
  .content-wrapper {
    padding: 60px;
  }
}

@media(max-width: 500px) {
  .banner {
    flex-direction: column;
  }
  h1 {
    font-size: 32px;
  }
  .content-wrapper {
    padding: 32px;
  }
  .header .content-wrapper {
    display: block;
  }
  .header img {
    margin-top: 40px;
  }
  .features .feature {
    display: flex;
    flex-direction: column-reverse;
    margin: 32px 0;
  }
  .features h1 {
    width: 100%;
  }
  .game .content-wrapper {
    display: flex;
    flex-direction: column-reverse;
  }
  img {
    max-width: 100%;
  }
}